import React, {useEffect} from 'react';
import Notepad from './components/pages/Notepad';
import firebase from './firebase';

function App() {

	useEffect(()=>{
		Notification.requestPermission().then(()=> firebase.messaging().getToken()).then(token=> console.warn('token', token));
	  });

	return (
		<div className='App'>
			<Notepad />
		</div>
	);
}

export default App;
